:root {
	--bg: #0b1020;
	--panel: #141a32;
	--muted: #9aa3b2;
	--text: #e8ecf5;
	--accent: #5b8cff;
	--accent-2: #49d49d;
	--danger: #ff6b6b;
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body { margin: 0; font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial; background: var(--bg); color: var(--text); }

.container { max-width: 980px; margin: 0 auto; padding: 24px; }

h1 { font-size: 28px; margin: 0 0 16px; }
h2 { font-size: 18px; margin: 16px 0 8px; }

.card { background: var(--panel); border: 1px solid rgba(255,255,255,0.06); border-radius: 12px; padding: 16px; }

.grid { display: grid; grid-template-columns: repeat(2, minmax(0,1fr)); gap: 12px; }
.label { display: block; }
label span { display: block; font-size: 12px; color: var(--muted); margin-bottom: 6px; }
input, textarea, select { width: 100%; padding: 10px 12px; border-radius: 8px; border: 1px solid rgba(255,255,255,0.08); background: #0f1530; color: var(--text); }
input:focus, textarea:focus, select:focus { outline: 2px solid var(--accent); border-color: transparent; }

.col-span-2 { grid-column: span 2; }

.actions { display: flex; justify-content: flex-end; margin-top: 12px; }
button { background: var(--accent); color: white; border: none; padding: 10px 14px; border-radius: 8px; cursor: pointer; font-weight: 600; }
button:disabled { opacity: 0.6; cursor: not-allowed; }

.error { color: var(--danger); font-size: 12px; margin-top: 6px; display: block; }
.muted { color: var(--muted); }
.dim { color: var(--muted); margin-left: 8px; }

.list { margin-top: 16px; }
ul { list-style: none; padding: 0; margin: 0; }
li { display: flex; gap: 8px; align-items: baseline; padding: 10px 0; border-bottom: 1px solid rgba(255,255,255,0.06); }

.toast { position: fixed; right: 20px; bottom: 20px; background: #0f1530; border: 1px solid rgba(255,255,255,0.08); padding: 10px 14px; border-radius: 10px; box-shadow: 0 4px 24px rgba(0,0,0,0.4); }
